import React from 'react'
import Link from 'gatsby-link'

import Principal from '../../assets/img/principal.jpg'

const Academics = () => (
  <div>
    <section className="nkc-banner hidden-print"></section>
    <main className="nkc-container clearfix">
      <section className="breadcumb hidden-print">
        <div className="container clearfix">
          <div className="col-lg-full col-md-full col-sm-full">
            <p><Link to="/">Home</Link> &rarr; Academics</p>
          </div>
        </div>
      </section>
      <div className="clearfix">
        <div className="col-lg-large col-md-large col-sm-full col-print-large clearfix">
          <div className="nkc-container__content clearfix">
            <h1 className="nkc-page-title">Academics</h1>
            <h2 id="principal">Message from the Principal</h2>
            <p className="nkc-para-intro">It gives me an immense pleasure to welcome you to the official website of North Kamrup College and I would like to thank you for taking the time to navigate its content.</p>
            <p>North Kamrup College, established in 1979 under the pioneering leadership of some visionaries of Baghmara area of Barpeta is a prominent seat of higher learning in Assam. Here, we strive to impart quality education to the learners who come from various parts of the region. We also try to imbibe the core values of life to the students so that they become good human beings and good citizens.</p>
            <p>Although, we provide conventional Arts Education, one of our priority areas is development of skills among the students. We aspire to introduce some vocational and job oriented courses in the near future so that our students can simultaneously acquire some additional diplomas and certificates along with their degrees during their stay.</p>
            <p>Centrally located in historically enlightened land of Baghmara, we have very good infrastructure in the campus. Our library is well equipped and we provide ample amount of resources to the students as well as faculties. We also have a fully furnished college canteen within the campus. Our faculty members are very concerned about the well being of the students and they always extend helping hands for the academic, spiritual and socio–cultural development as well. Maintenance of discipline, punctuality, and regularity is top priority of this institution. The Internal Quality Assurance Cell (I. Q. A. C.) of the college takes care for improving quality of education and suggests measures for overall development of this institution. We can assure that after passing out, our students will fondly remember the college and call themselves a proud alumnus of North Kamrup College.</p>
            <ul className="li-unstyled">
              <li><strong>Dr. Surajit Barman</strong></li>
              <li>Principal, North Kamrup College</li>
            </ul>
            <center className="hidden-sm">· · ·</center>
            <h2 id="faculties">Faculties</h2>
            <ul className="li-unstyled">
              <li><Link to="/departments/#assamese">Department of Assamese</Link></li>
              <li><Link to="/departments/#bodo">Department of Bodo</Link></li>
              <li><Link to="/departments/#economics">Department of Economics</Link></li>
              <li><Link to="/departments/#education">Department of Education</Link></li>
              <li><Link to="/departments/#english">Department of English</Link></li>
              <li><Link to="/departments/#history">Department of History</Link></li>
              <li><Link to="/departments/#philosophy">Department of Philosophy</Link></li>
              <li><Link to="/departments/#political-science">Department of Political Science</Link></li>
            </ul>
            <center className="hidden-sm">· · ·</center>
            <h2 id="courses-offered">Courses Offered</h2>
            <p>Under the aegis of A. H. S. E. C. and Gauhati University, the college offers the priviledge of imparting studies in Arts Faculty both in H. S. and Degree levels.</p>
            <p><Link to="/academics/courses-offered">Learn more &rarr;</Link></p>
          </div>
        </div>
        <div className="col-lg-medium col-md-medium col-sm-full col-print-medium">
          <div className="nkc-container__sidebar">
            <div className="responsive-table">
              <table className="table-no-border nkc-feature-table">
                <tbody>
                  <tr>
                    <td colSpan="2">
                      <h2><span className="fa fa-fw fa-bookmark-o"></span> Principal, NKC</h2>
                    </td>
                  </tr>
                  <tr>
                    <td colSpan="2"><img src={Principal} style={{ width: '100%' }} title="Dr. Surajit Barman" alt="Dr. Surajit Barman" /></td>
                  </tr>
                  <tr>
                    <td>Name</td>
                    <td>Dr. Surajit Barman</td>
                  </tr>
                  <tr>
                    <td>Joined</td>
                    <td>2014</td>
                  </tr>
                  <tr>
                    <td>Qualifications</td>
                    <td>M. A., M. Phil., Ph. D., P. G. D. E. E.</td>
                  </tr>
                  <tr>
                    <td>Address</td>
                    <td>The Principal,<br />
                      North Kamrup College, Baghmara, Barpeta, Assam, 781328
                    </td>
                  </tr>
                  <tr>
                    <td>Telephone</td>
                    <td><a href="tel:+913666268747">+91 366 626 8747</a></td>
                  </tr>
                  <tr>
                    <td>Email</td>
                    <td><a href="mailto:principal@northkamrupcollege.in">Official Email</a></td>
                  </tr>
                  <tr>
                    <td>Website</td>
                    <td><Link to="/" title="Official Website of North Kamrup College">Official Website</Link></td>
                  </tr>
                </tbody>
              </table>
            </div>
          </div>
        </div>
      </div>
    </main>
    <div className="spacer"></div>
  </div>
)

export default Academics



